.home-container {
    width: 40vw;
    height: 18rem;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .2); 
    border-radius: 20px;
    background-color: rgba(255, 255, 255, .15);
    backdrop-filter: blur(15px);
    z-index:10;
  }

.intro{
  font-weight: bolder;
  text-align: center;
  padding-top: 1rem;
}

.intro-text{
  padding-left: 1rem;
  padding-right: 1rem;
}

@media screen and (max-width: 1553px) {
  .home-container{
    height: 20rem
  }
 }

 @media screen and (max-width: 1148px) {
  .home-container{
    width: 55vw;
    height: 20rem;
  }
 }

 @media screen and (max-width: 918px) {
  .home-container{
    width: 65vw;
    height: 20rem
  }
 }

 @media screen and (max-width: 705px) {
  .home-container{
    width: 70vw;
    height: 22rem;
  }
 }

 @media screen and (max-width: 560px) {
  .home-container{
    width: 80vw;
    height: 22rem;
  }
 }

 @media screen and (max-width: 523px) {
  .home-container{
    width: 90vw;
    height: 23rem;
  }
  .intro{
    font-size:1.5rem;
  }
  .intro-text{
    font-size: 1.2rem;
  }
 }

 @media screen and (max-width: 422px) {
  .home-container{
    width: 80vw;
    height: 23rem;
  }
  .intro{
    font-size:1.2rem;
  }
  .intro-text{
    font-size: 1.0rem;
  }
 }

 @media screen and (max-width: 317px) {
  .home-container{
    width: 90vw;
    height: 23rem;
  }
 }

 @media screen and (max-width: 280px) {
  .home-container{
    width: 80vw;
    height: 25rem;
  }
 }

 @media screen and (max-width: 252px) {
  .home-container{
    width: 80vw;
    height: 27rem;
  }
  .intro{
    font-size:1.0rem;
  }
 }