.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.back-to-top {
  position: fixed;
  bottom: 30px;
  right: 30px;
  font-size: 1.2rem;
  background: #1B5996;
  color: white;
  cursor: pointer;
  border: 1px solid #3B79B2;
  border-radius: 100px;
  z-index:10;
}

.back-to-top:hover {
  background: #3B79B2;
}


@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-height: 590px) {
  #contact {
    min-height: 190vh !important;
  }
}

@media screen and (max-height: 455px) {
  #contact {
    min-height: 200vh !important;
  }
}

@media screen and (max-height: 421px) {
  #contact {
    min-height: 250vh !important;
  }
}

@media screen and (max-height: 293px) {
  #contact {
    min-height: 320vh !important;
  }
}


@media screen and (max-height: 278px) {
  #contact {
    min-height: 350vh !important;
  }
}